import { Component, Input, OnInit, ViewEncapsulation, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

import { Notification } from 'src/app/layout/common/notifications/notifications.types';
import { NotificationsService } from 'src/app/layout/common/notifications/notifications.service';

@Component({
  selector: 'app-notification-list',
  templateUrl: './notification-list.component.html',
  styleUrls: ['./notification-list.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class NotificationListComponent implements OnInit {

  // Private
  private _notifications: Notification[];
  private _unsubscribeAll: Subject<any>;

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _notificationsService: NotificationsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

   }

  @Input()
  set notifications(value: Notification[])
  {
      // Store the value
      this._notificationsService.store(value);
  }

  get notifications(): Notification[]
  {
      return this._notifications;
  }

  ngOnInit(): void {

     // Subscribe to notification changes
     this._notificationsService.notifications$
     .pipe(takeUntil(this._unsubscribeAll))
     .subscribe((notifications: Notification[]) => {

         // Load the notifications
         this._notifications = notifications;


     });

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

}
